  font-size: 1.25rem;
  color: #ffb71b;
  position: relative;
}
.joinbtn:after {
    width: 100%;
    content: "";
    position: absolute;
    left: 0;
    -webkit-transform: scaleY(0.5);
    -webkit-transform-origin: 0 0;
    top: 0;
    border-top: 0.03125rem solid #efefef;
}
